<h2 id="form-表单" tabindex="-1"><a class="header-anchor" href="#form-表单" aria-hidden="true">#</a> Form 表单</h2>
<p>表单组件包含数据录入，表单验证，样式</p>
<p>有两种排列模式：
1、垂直排列
2、行内排列</p>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="行内排列" tabindex="-1"><a class="header-anchor" href="#行内排列" aria-hidden="true">#</a> 行内排列</h4>
<div class="component-box">
    <div class="component-box-top"><ClientOnly>
      <wd-form
        ref="userInfoForm"
        label-width="100px"
        label-align="right"
        size="small"
        inline
        :model="userInfo"
        :validate-on-rule-change="true"
        :rules="formRules"
        :show-message="true"
        label-position="left"
      >
        <wd-form-item label="姓名" prop="name">
          <wd-input
            type="text"
            placeholder="请输入姓名"
          ></wd-input>
        </wd-form-item>
        <wd-form-item label="性别" prop="gender">
          <wd-radio
            :checked="isMale"
            name="color"
            @change="handleRadioChange"
          >
            男
          </wd-radio>
          <wd-radio
            :checked="isFemale"
            name="color"
            @change="handleRadioChange"
          >
            女
          </wd-radio>
        </wd-form-item>
        <wd-form-item label="婚姻状况" prop="isMerried">
          <wd-checkbox
            :checked="true"
            @change="handleCheckboxChange"
          >
            已婚
          </wd-checkbox>
        </wd-form-item>
        <wd-form-item label="年龄" prop="age">
          <wd-input-number
            max="20"
            min="0"
            @input="handleInputNumber"
            @change="handleChangeNumber"
            type="text"
          ></wd-input-number>
        </wd-form-item>
        <wd-form-item label="学历">
          <wd-select
            v-model="selectedValue"
            @change="selectedChangeHandler"
            placeholder="请选择学历"
          >
            <wd-option label="研究生" value="baidu"></wd-option>
            <wd-option label="本科" value="google"></wd-option>
            <wd-option label="专科" value="bing"></wd-option>
          </wd-select>
        </wd-form-item>
      </wd-form>
    </ClientOnly></div>
    <div class="component-box-bottom">
        <div class="component-title">表单</div>
        <div class="component-desc">
            设置inline属性可以让表单项以行内样式排列
        </div>
    </div>
</div> 
