<h4 id="垂直排列" tabindex="-1"><a class="header-anchor" href="#垂直排列" aria-hidden="true">#</a> 垂直排列</h4>
<div class="component-box">
    <div class="component-box-top"><ClientOnly>
      <wd-form
        ref="userInfoForm"
        label-width="100px"
        label-align="right"
        size="small"
        :model="userInfo"
        :validate-on-rule-change="true"
        :rules="formRules"
